browser.runtime.onConnect.addListener(port => {
  let connected = true;

  port.onDisconnect.addListener(() => (connected = false));

  port.onMessage.addListener(async ({ type, name, args }) => {
    if (type !== 'proxy-request') {
      return;
    }

    console.debug('proxy-request', name, args, port);
    const { default: fn } = await import(`./services/${name}.js`);

    try {
      const data = await fn(...args, port);
      console.debug('proxy-success', name, args, port, data);
      connected && port.postMessage({ type: 'proxy-success', name, data });
    } catch (e) {
      const data = JSON.parse(JSON.stringify(e));
      console.debug('proxy-error', name, args, port, data, e);
      connected && port.postMessage({ type: 'proxy-error', name, data });
    }
  });
});
