import { ReplaySubject } from 'rxjs';

const store$ = new ReplaySubject(1000);

export const publish = data =>
  Object.entries(data).forEach(([key, value]) => store$.next({ [key]: value }));

export const subscribe = (next, error, complete) =>
  store$.subscribe(next, error, complete);

export const clear = () =>
  publish({
    access_token: null,
    accounts: null,
    activeRequests: 0,
    authenticators: null,
    autoFill: {},
    formData: {},
    isHardTokenUser: false,
    passwordmanagers: null,
    privateKey: null,
    refresh_token: null,
    saaspassId: null,
  });

clear();

export const store = {};
subscribe(data => Object.assign(store, data));

subscribe(data => console.debug('store', data));
