browser.runtime.onInstalled.addListener(() => {
  initializeOptions();
  reloadPortalTabs();
});

const initializeOptions = async () => {
  const { autoFillOnLoad } = await browser.storage.local.get();
  if (autoFillOnLoad === undefined) {
    browser.storage.local.set({ autoFillOnLoad: false });
  }
};

const reloadPortalTabs = async () => {
  const tabs = await browser.tabs.query({ url: `${api.baseURL}/*` });
  tabs.forEach(tab => browser.tabs.reload(tab.id));
};
